import React, { useState, useEffect } from 'react';
import { useHistory } from "react-router";
import { Row, Col, Card, Descriptions, Image, Tabs, Empty, Modal, PageHeader, Skeleton } from 'antd';
import { Main } from '../styled';
import { Countdown } from '../../components/countdown';
import { Button } from '../../components/buttons/buttons';
import { get_appointment_detail } from '../../api';
import { uri_segment } from '../../utility/utility';

const Detail = (props) => {

    const appointment_id = props?.match?.params?.id;
    const [alert, setAlert] = useState(null);
    const [data, setData] = useState({});
    const [loading, setLoading] = useState(true);
    const from = uri_segment(2);
    const history = useHistory();

    const getData = async() => {
        setLoading(true);
        const {
            result, error, message
        } = await get_appointment_detail(appointment_id);
        setLoading(false);
        if(error) {
            setAlert('error:' + message);
        } else {
            setData(result.data);
        }
    }

    const confirmStartConsul = async () => {
        Modal.confirm({
            className: 'modal-content-center',
            icon: null,
            centered: true,
            content: <div className="text-center">
                <h1>Yakin mulai konsultasi?</h1>
                Waktu mulai akan dicatat
            </div>,
            okText: 'Ya, Lanjutkan',
            cancelText: 'Batalkan',
            onOk: () => {
                history.push('/doctor/worklist/' + appointment_id + '/start')
            }
        });
    }

    useEffect(() => {
        getData();
    }, []);

    return(<>
        <PageHeader
            onBack={() => history.goBack()}
            title="Detail Perjanjian"
        />
        <Main>
            {alert}
            <Row gutter={24}>
                <Col lg={12} xs={24}>
                    <Card title="Data Pasien" bordered={true}>
                        {loading ? <Skeleton avatar/> : (<>
                            <figure className="text-center">
                                <Image
                                    width={200}
                                    preview={false}
                                    src={data?.patient_pic}
                                />
                            </figure>
                            <Descriptions bordered column={1}>
                                <Descriptions.Item label="Tanggal Konsultasi">
                                    {data.id_consul_date} {data.consul_time}
                                    {   data.status === 'waiting_consul' && (<>
                                        <br/>
                                        <Countdown date={data.consul_date} time={data.consul_time} />
                                        </>)
                                    }
                                </Descriptions.Item>
                                <Descriptions.Item label="Nama Pasien">{data.patient_name}</Descriptions.Item>
                                <Descriptions.Item label="Umur">{data.age}</Descriptions.Item>
                                <Descriptions.Item label="Keluhan Utama">{data.main_complaint}</Descriptions.Item>
                                <Descriptions.Item label="Riwayat Penyakit">{data.disease_history ? data.disease_history : '-'}</Descriptions.Item>
                                <Descriptions.Item label="Alergi">{data.allergy ? data.allergy : '-'}</Descriptions.Item>
                                <Descriptions.Item label="Suhu Badan">{data.body_temperature ? data.body_temperature : '-'} ℃</Descriptions.Item>
                                <Descriptions.Item label="Tekanan Darah">{data.blood_pressure ? data.blood_pressure : '- '} mmHg</Descriptions.Item>
                                <Descriptions.Item label="Berat Badan">{data.weight ? data.weight : '-'} Kg</Descriptions.Item>
                                <Descriptions.Item label="Tinggi Badan">{data.height ? data.height : '-'} Cm</Descriptions.Item>
                            </Descriptions>
                            {data.status === 'waiting_consul' && (<> <br/>
                                <Button type="primary" block={true} size="large" onClick={confirmStartConsul}>
                                        Mulai Konsultasi
                                </Button>
                            </>)}
                            {data.status === 'done' && (<> <br/>
                                <Button type="primary" block={true} size="large" onClick={() => history.push(`/doctor/history/soap/${appointment_id}`)}>
                                        Update Soap
                                </Button>
                            </>)}
                        </>)}
                    </Card>
                </Col>
                <Col lg={12} xs={24}>
                    <Row gutter={[24, 24]}>
                        <Col lg={24}>
                            <Card title="Subjective" bordered={true}>
                                { loading ? <Skeleton paragraph={{rows: 1}} /> :
                                    data?.subjective ? data.subjective :
                                    <Empty image={Empty.PRESENTED_IMAGE_SIMPLE}/>
                                }
                            </Card>
                        </Col>
                        <Col lg={24}>
                            <Card title="Objective" bordered={true}>
                                { loading ? <Skeleton paragraph={{rows: 1}} /> :
                                    data?.objective ? data.objective :
                                    <Empty image={Empty.PRESENTED_IMAGE_SIMPLE}/>
                                }
                            </Card>
                        </Col>
                        <Col lg={24}>
                            <Card title="Assesment" bordered={true}>
                                { loading ? <Skeleton paragraph={{rows: 1}} /> :
                                    data?.assesment ? data.assesment :
                                    <Empty image={Empty.PRESENTED_IMAGE_SIMPLE}/>
                                }
                            </Card>
                        </Col>
                        <Col lg={24}>
                            <Card title="Plan" bordered={true}>
                                { loading ? <Skeleton paragraph={{rows: 1}} /> :
                                    data?.plan ? data.plan :
                                    <Empty image={Empty.PRESENTED_IMAGE_SIMPLE}/>
                                }
                            </Card>
                        </Col>
                        <Col lg={24}>
                            <Card>
                                { loading ? <Skeleton paragraph={{rows: 1}} /> :
                                <Tabs defaultActiveKey="1" type="card" style={{ marginBottom: 32 }}>
                                    <Tabs.TabPane tab="Farmasi" key="1">
                                        <p>
                                            <b>Rekomendasi:</b> <br/>
                                            {data.phar_recom}
                                        </p>
                                        <p>
                                            <b>Diagnosa:</b> <br/>
                                            {data.phar_diagnosis}
                                        </p>
                                        <p>
                                            <b>Alergi:</b> <br/>
                                            {data.phar_allergy}
                                        </p>
                                    </Tabs.TabPane>
                                    <Tabs.TabPane tab="Laboratorium" key="2">
                                        <p>
                                            <b>Rekomendasi:</b> <br/>
                                            {data.lab_recom}
                                        </p>
                                        <p>
                                            <b>Diagnosa:</b> <br/>
                                            {data.lab_diagnosis}
                                        </p>
                                        <p>
                                            <b>Alergi:</b> <br/>
                                            {data.lab_allergy}
                                        </p>
                                    </Tabs.TabPane>
                                    <Tabs.TabPane tab="Radiologi" key="3">
                                        <p>
                                            <b>Rekomendasi:</b> <br/>
                                            {data.rad_recom}
                                        </p>
                                        <p>
                                            <b>Diagnosa:</b> <br/>
                                            {data.rad_diagnosis}
                                        </p>
                                        <p>
                                            <b>Alergi:</b> <br/>
                                            {data.rad_allergy}
                                        </p>    
                                    </Tabs.TabPane>
                                </Tabs>
                                }
                            </Card>
                        </Col>
                    </Row>
                </Col>
            </Row>
        </Main>
    </>);
}

export default Detail;