import React from 'react';
const CountdownComp = require('react-countdown').default;

let config = {
    size: 'small',
    className: null
}

const Completionist = () => <div className="color-error">Jam sudah lewat</div>;
const renderer = ({month, days, hours, minutes, seconds, completed }) => {
    if (completed) {
        // Render a complete state
        return <Completionist />;
    } else {
        // Render a countdown

        const info =  month > 0 ? <>{month} Bulan {days} Hari</> :
                        days > 0 ?  <>{days} Hari {hours} Jam</> :
                        hours > 0 ?  <>{hours} Jam {minutes} Menit</> :
                        minutes > 0 ?  <>{minutes} Menit {seconds} Detik</> : <>{seconds} Detik</>;
        return (
            config?.size === 'small' && (<small className={config?.className}>{info}</small>)
            ||
            config?.size === 'medium' && <div className={config?.className}>{info}</div>
        );
    }
};

const Countdown = (props) => {

    let {
        date = null,
        time = null,
        render = null,
        ...otherProps
    } = props;
    
    config = {...config, ...otherProps};

    const full_date = time ? date + ' ' + time : date;
    render = render ? render : renderer;    

    return(
        <CountdownComp date={full_date} renderer={render}/>
    );
}

export { Countdown };