import React from 'react';
import { useHistory } from "react-router";
import Cookies from 'js-cookie';
import { Menu } from 'antd';
import { NavLink, useRouteMatch } from 'react-router-dom';
import FeatherIcon from 'feather-icons-react';
import propTypes from 'prop-types';

const MenuItems = ({ darkMode, toggleCollapsed, topMenu }) => {
  const { path } = useRouteMatch();
  const history = useHistory();

  const pathName = window.location.pathname;
  const pathArray = pathName.split(path);
  const mainPath = pathArray[1];
  const mainPathSplit = mainPath.split('/');

  const [openKeys, setOpenKeys] = React.useState(
    !topMenu ? [`${mainPathSplit.length > 2 ? mainPathSplit[1] : 'dashboard'}`] : [],
  );

  const onOpenChange = keys => {
    setOpenKeys(keys[keys.length - 1] !== 'recharts' ? [keys.length && keys[keys.length - 1]] : keys);
  };

  const onClick = item => {
    if (item.keyPath.length === 1) setOpenKeys([]);
  };

  const logout = () => {
    localStorage.clear();
    Cookies.remove('token');
    history.push('/');
    window.location.reload();
  }

  return (
    <Menu
      onOpenChange={onOpenChange}
      onClick={onClick}
      mode={!topMenu || window.innerWidth <= 991 ? 'inline' : 'horizontal'}
      theme={darkMode && 'dark'}
      // // eslint-disable-next-line no-nested-ternary
      defaultSelectedKeys={
        !topMenu
          ? [
              `${
                mainPathSplit.length === 1 ? 'home' : mainPathSplit.length === 2 ? mainPathSplit[1] : mainPathSplit[2]
              }`,
            ]
          : []
      }
      defaultOpenKeys={!topMenu ? [`${mainPathSplit.length > 2 ? mainPathSplit[1] : 'dashboard'}`] : []}
      overflowedIndicator={<FeatherIcon icon="more-vertical" />}
      openKeys={openKeys}
    >
      <Menu.Item
        icon={
          <NavLink className="menuItem-iocn" to={`/doctor`}>
            <i aria-hidden="true" className="fa fa-home"></i>
          </NavLink>
        }
        key="dashboard"
      >
        <NavLink onClick={toggleCollapsed} to={`/doctor`}>
          Dashboard
        </NavLink>
      </Menu.Item>

      <Menu.Item
        icon={
          <NavLink className="menuItem-iocn" to={`/doctor/worklist`}>
            <i aria-hidden="true" className="fa fa-wheelchair"></i>
          </NavLink>
        }
        key="worklist"
      >
        <NavLink onClick={toggleCollapsed} to={`/doctor/worklist`}>
          Worklist
        </NavLink>
      </Menu.Item>

      <Menu.Item
        icon={
          <NavLink className="menuItem-iocn" to={`/doctor/appointment`}>
            <i aria-hidden="true" className="fa fa-calendar"></i>
          </NavLink>
        }
        key="appointment"
      >
        <NavLink onClick={toggleCollapsed} to={`/doctor/appointment`}>
          Perjanjian Mendatang
        </NavLink>
      </Menu.Item>

      <Menu.Item
        icon={
          <NavLink className="menuItem-iocn" to={`/doctor/history`}>
            <i aria-hidden="true" className="fa fa-calendar-check-o"></i>
          </NavLink>
        }
        key="history"
      >
        <NavLink onClick={toggleCollapsed} to={`/doctor/history`}>
          Riwayat Perjanjian
        </NavLink>
      </Menu.Item>

      <Menu.Item
        icon={
          <NavLink className="menuItem-iocn" to={`/doctor/setting`}>
            <i aria-hidden="true" className="fa fa-cog"></i>
          </NavLink>
        }
        key="setting"
      >
        <NavLink onClick={toggleCollapsed} to={`/doctor/setting`}>
          Pengaturan Akun
        </NavLink>
      </Menu.Item>
      
      <Menu.Item
        icon={
          <NavLink className="menuItem-iocn" to="#">
            <i aria-hidden="true" className="fa fa-sign-out"></i>
          </NavLink>
        }
        key="logout"
        onClick={logout}
      >
        <NavLink onClick={logout} to="#">
          Keluar
        </NavLink>
      </Menu.Item>

    </Menu>
  );
};

MenuItems.propTypes = {
  darkMode: propTypes.bool,
  topMenu: propTypes.bool,
  toggleCollapsed: propTypes.func,
};

export default MenuItems;
