import Axios from 'axios';
import Cookies from 'js-cookie';
import store from '../redux/store';
import { loginModal } from '../redux/authentication/actionCreator';

const rootUrl = '/';
const baseUrl = rootUrl + '/api';
let res = [];
let result = {
    result: null,
    error: true,
    message: null,
    errors: {},
    forceStop: false
};

Axios.interceptors.request.use(function (config) {
    const token = Cookies.get('token');
    config.headers.Authorization =  'Bearer ' + token;
    return config;
});

Axios.interceptors.response.use(
    response => {
        let status = response.data?.status ? response.data.status : false;
        let message = response.data?.message ? response.data.message : response.message;
        if(!message) message = 'Message Null';
        let errors = [];
        if(response.data?.errors) {
            Object.keys(response.data.errors).map(key => {
                let _data = response.data.errors[key];
                return errors.push({
                    name: key,
                    errors: _data
                })
            })
        }

        if(status === true) {
            result = {
                ...result,
                result: response.data,
                error: false,
                message: message,
                errors: errors,
                forceStop: false,
            };
        } else {
            result = {
                ...result,
                result: response.data,
                error: true,
                message: message,
                errors: errors,
                forceStop: false,
            };
        }

        return result;
},  error => {
        let response = error?.response;
        let message = response?.data?.message ? response.data.message : error?.message;
        let errors = [];
        if(response?.data?.errors) {
            Object.keys(response?.data.errors).map(key => {
                let _data = response.data.errors[key];
                return errors.push({
                    name: key,
                    errors: _data
                })
            })
        }

        if(response?.status === 401) {
            store.dispatch(loginModal(true));
            result = {
                ...result,
                result: response?.data,
                error: true,
                message: message,
                errors: errors,
                forceStop: true,
            };
        } else {
            result = {
                ...result,
                result: response?.data,
                error: true,
                message: message,
                errors: errors,
                forceStop: false,
            };
        }
        return Promise.reject(result);
});

const createParams = (params) => {
    const qs = Object.keys(params)
    .map(key => `${key}=` + (!params[key] ? '' : params[key]))
    .join('&');
    return qs;
}

const api = (endpoint) => {
    return `${baseUrl}/${endpoint}`;
}

const _get = async (url, data=null) => {
    await Axios.get(url, data)
    .then(response => res = response)
    .catch(error => res = error);
    return res;
}

const _post = async (url, data) => {
    await Axios.post(url, data)
    .then(response => res = response)
    .catch(error => res = error);
    return res;
}

const _put = async (url, data) => {
    await Axios.put(url, data)
    .then(response => res = response)
    .catch(error => res = error);
    return res;
}

const _delete = async (url, data) => {
    await Axios.delete(url, data)
    .then(response => res = response)
    .catch(error => res = error);
    return res;
}

const _login = async (data) => {
    return await _post(api('auth/login'), data);
}

const get_worklist = async(filters) => {
    return await _post(api(`doctor/worklist`), filters);
}

const get_appointment_history = async(filters) => {
    return await _post(api(`doctor/appointment/history`), filters);
}

const get_appointment_detail = async(id, fields) => {
    return await _post(api(`doctor/appointment/${id}`), fields);
}

const input_soap = async (appointment_id, fields) => {
    return await _post(api(`doctor/soap/${appointment_id}/input`), fields);
}

const update_soap = async (appointment_id, fields) => {
    return await _post(api(`doctor/soap/${appointment_id}/update`), fields);
}

const get_appointment_incoming = async(filters) => {
    return await _post(api(`doctor/appointment/incoming`), filters);
}

const update_account = async (fields) => {
    return await _post ( api(`account/update`), fields );
}

const update_password = async (fields) => {
    return await _post (api(`account/update_password`), fields );
}

const dashboard_data = async () => {
    return await _post (api(`dashboard`), {
        doctor_dashboard:  true
    });
}

export {
    createParams,
    api,
    _get,
    _post,
    _put,
    _delete,
    _login,
    get_worklist,
    get_appointment_incoming,
    get_appointment_history,
    get_appointment_detail,
    input_soap,
    update_soap,
    update_account,
    update_password,
    dashboard_data
};

export default api;