import React, { useState, useEffect } from 'react';
import { Row, Col, Card, Descriptions, Form, Input, PageHeader } from 'antd';
import { useHistory } from "react-router";
import { useDispatch } from 'react-redux';
import { Main } from '../styled';
import { Countdown } from '../../components/countdown';
import { Button } from '../../components/buttons/buttons';
import { get_appointment_detail, input_soap } from '../../api';
import {
    loadingStart, loadingContent, loadingSuccess, loadingError, closeLoading
} from '../../redux/loadingmodal/actionCreator';
import { Link } from 'react-router-dom';

const Start = (props) => {

    const dispatch = useDispatch();

    const appointment_id = props?.match?.params?.id;
    const [alert, setAlert] = useState('Menyimpan data');
    const [loadingStatus, setLoadingstatus] = useState('loading');
    const [data, setData] = useState({});
    const [hidePatient, setHidePatient] = useState(true);
    const [form] = Form.useForm();
    const history = useHistory();

    const getData = async() => {
        const {
            result, error, message
        } = await get_appointment_detail(appointment_id,{start: true});

        if(error) {
            setAlert('error:' + message);
        } else {
            setData(result.data);
            form.setFieldsValue({
                subjective: result?.data?.subjective,
                objective: result?.data?.objective,
                assesment: result?.data?.assesment,
                plan: result?.data?.plan,
                phar_recom: result?.data?.phar_recom,
                phar_diagnosis: result?.data?.phar_diagnosis,
                phar_allergy: result?.data?.phar_allergy,
                rad_recom: result?.data?.rad_recom,
                rad_diagnosis: result?.data?.rad_diagnosis,
                rad_allergy: result?.data?.rad_allergy,
                lab_recom: result?.data?.lab_recom,
                lab_diagnosis: result?.data?.lab_diagnosis,
                lab_allergy: result?.data?.lab_allergy,
            })
        }
    }

    useEffect(() => {
        getData();
    }, []);

    const onSubmit = async (fields) => {
        
        dispatch(loadingStart());
        dispatch(loadingContent('proses menyimpan data...'));

        const {
            error, message
        } = await input_soap(appointment_id, fields);

        if(error) {
            dispatch(loadingError());
            dispatch(loadingContent(message));
            setAlert(message);
            setTimeout(closeLoading, 3000);
            return;
        }

        dispatch(loadingSuccess());
        dispatch(loadingContent('Berhasil menyimpan data'));

        setTimeout(() => {
            dispatch(closeLoading());
            history.push('/doctor/worklist');
        }, 3000);
    }
    
    return(<>
        <PageHeader
            onBack={() => history.goBack()}
            title={"Proses Telekonsultasi: " + appointment_id}
        />
        <Main>
            <Row gutter={[24,24]}>
                <Col lg={24} xs={24} className="text-center">
                    <a href={data?.start_url} target="__blank">
                        <Button type="primary" size="large" block={true}>Buka Zoom Meeting</Button>
                    </a>
                </Col>
                <Col span={24}>
                    <Form
                        form={form}
                        onFinish={onSubmit}
                        layout="vertical"
                    >
                        <Row gutter={[24,24]}>
                            <Col lg={24} xs={24}>
                                <Card
                                    title="Data Pasien"
                                    bordered={true}
                                    extra={
                                        <Button type={hidePatient ? 'primary' : 'danger'} onClick={() => setHidePatient(!hidePatient)}>
                                            { hidePatient ? 'Tampilkan' : 'Sembunyikan' }
                                        </Button>
                                    }
                                >
                                    <Descriptions bordered column={2} style={{
                                        display: ( hidePatient ? 'none' : '' )
                                    }}>
                                        <Descriptions.Item label="Mulai Konsultasi">
                                            {data.id_consul_date} {data.consul_time}
                                        </Descriptions.Item>
                                        <Descriptions.Item label="Selesai Konsultasi">
                                            {data.consul_finish_id_date} <br/>
                                        </Descriptions.Item>
                                        <Descriptions.Item label="Konsultasi Selesai Dalam">
                                            {data?.consul_finish_id_date && (<Countdown className="color-error" size="medium" date={data.consul_finish_date} time={data.consul_finish_time} />)}
                                            <small className="color-error">Dokter tidak harus menyelesaikan konsultasi ketika durasi habis, sesuaikan dengan kebutuhan pasien</small>
                                        </Descriptions.Item>
                                        <Descriptions.Item label="Nama Pasien">{data.patient_name}</Descriptions.Item>
                                        <Descriptions.Item label="Umur">{data.age}</Descriptions.Item>
                                        <Descriptions.Item label="Keluhan Utama">{data.main_complaint}</Descriptions.Item>
                                        <Descriptions.Item label="Riwayat Penyakit">{data.disease_history ? data.disease_history : '-'}</Descriptions.Item>
                                        <Descriptions.Item label="Alergi">{data.allergy ? data.allergy : '-'}</Descriptions.Item>
                                        <Descriptions.Item label="Suhu Badan">{data.body_temperature ? data.body_temperature : '-'} ℃</Descriptions.Item>
                                        <Descriptions.Item label="Tekanan Darah">{data.blood_pressure ? data.blood_pressure : '- '} mmHg</Descriptions.Item>
                                        <Descriptions.Item label="Berat Badan">{data.weight ? data.weight : '-'} Kg</Descriptions.Item>
                                        <Descriptions.Item label="Tinggi Badan">{data.height ? data.height : '-'} Cm</Descriptions.Item>
                                    </Descriptions>
                                </Card>
                            </Col>
                            <Col lg={24} xs={24}>
                                <Row gutter={[24,24]}>
                                    <Col lg={12} xs={24}>
                                        <Card title="Subjective">
                                            <Form.Item name="subjective">
                                                <Input.TextArea placeholder="..." rows={5} />
                                            </Form.Item>
                                        </Card>
                                    </Col>
                                    <Col lg={12} xs={24}>
                                        <Card title="Objective">
                                            <Form.Item name="objective">
                                                <Input.TextArea placeholder="..." rows={5} />
                                            </Form.Item>
                                        </Card>
                                    </Col>
                                    <Col lg={12} xs={24}>
                                        <Card title="Assesment">
                                            <Form.Item name="assesment">
                                                <Input.TextArea placeholder="..." rows={5} />
                                            </Form.Item>
                                        </Card>
                                    </Col>
                                    <Col lg={12} xs={24}>
                                        <Card title="Plan">
                                            <Form.Item name="plan">
                                                <Input.TextArea placeholder="..." rows={5} />
                                            </Form.Item>
                                        </Card>
                                    </Col>
                                </Row>
                            </Col>
                            <Col span={24}>
                                <Row gutter={[24, 24]}>
                                    <Col lg={8} xs={12}>
                                        <Card
                                            title="Farmasi"
                                        >
                                            <Form.Item
                                                label="Rekomendasi"
                                                name="phar_recom"
                                            >
                                                <Input.TextArea placeholder="..." />
                                            </Form.Item>

                                            <Form.Item
                                                label="Diagnosa"
                                                name="phar_diagnosis"
                                            >
                                                <Input.TextArea placeholder="..." />
                                            </Form.Item>

                                            <Form.Item
                                                label="Alergi"
                                                name="phar_allergy"
                                            >
                                                <Input.TextArea placeholder="..." />
                                            </Form.Item>
                                        </Card>
                                    </Col>

                                    <Col lg={8} xs={12}>
                                        <Card
                                            title="Laboratorium"
                                        >
                                            <Form.Item
                                                label="Rekomendasi"
                                                name="lab_recom"
                                            >
                                                <Input.TextArea placeholder="..." />
                                            </Form.Item>

                                            <Form.Item
                                                label="Diagnosa"
                                                name="lab_diagnosis"
                                            >
                                                <Input.TextArea placeholder="..." />
                                            </Form.Item>

                                            <Form.Item
                                                label="Alergi"
                                                name="lab_allergy"
                                            >
                                                <Input.TextArea placeholder="..." />
                                            </Form.Item>
                                        </Card>
                                    </Col>

                                    <Col lg={8} xs={12}>
                                        <Card
                                            title="Radiologi"
                                        >
                                            <Form.Item
                                                label="Rekomendasi"
                                                name="rad_recom"
                                            >
                                                <Input.TextArea placeholder="..." />
                                            </Form.Item>

                                            <Form.Item
                                                label="Diagnosa"
                                                name="rad_diagnosis"
                                            >
                                                <Input.TextArea placeholder="..." />
                                            </Form.Item>

                                            <Form.Item
                                                label="Alergi"
                                                name="rad_allergy"
                                            >
                                                <Input.TextArea placeholder="..." />
                                            </Form.Item>
                                        </Card>
                                    </Col>
                                </Row>
                            </Col>
                        </Row> <br/>
                        <Form.Item>
                            <Button type="primary" htmlType="submit" size="large" block={true}>Selesai Telekonsultasi</Button>
                        </Form.Item>
                    </Form>
                </Col>
            </Row>
        </Main>
    </>);
}

export default Start;