import React, { useEffect, useState } from 'react';
import { Row, Col, Empty, Card, Skeleton, Pagination, PageHeader } from 'antd';
import { useHistory } from "react-router";
import { Main } from '../styled';
import { AutoComplete } from '../../components/autoComplete/autoComplete';
import { AlertError } from '../../components/alerts/alerts';
import { PatientCard } from '../../components/cards';

import { get_worklist } from '../../api';
let _wait = null;

const Worklist = (props) => {

    const [data, setData] = useState([]);
    const [alert, setAlert] = useState(null);
    const [loading, setLoading] = useState(true);
    const [filters, setFilter] = useState({
        data_per_page: 25
    });
    const [raw, setRaw] = useState({});
    const history = useHistory();

    const getData = async () => {
        setData([]);
        const {
            result, error, message
        } = await get_worklist(filters);
        setLoading(false);
        if(error) {
            setAlert(<AlertError message={message}/>);
        } else {
            setData(result.data.data);
            setRaw(result.data);
        }
    }

    const onSearch = (e) => {
        setLoading(true);
        if(_wait) clearTimeout(_wait)
        _wait = setTimeout(() => setFilter({ ...filters, query: e }), 1000);
    }

    const changePagination = (page, size) => {
        setFilter({...filters, page: page, data_per_page: size});
    }

    useEffect(() => {
        getData();
    }, [filters])

    return(<>
        <PageHeader
            onBack={() => history.goBack()}
            title="Worklist"
            buttons={[
                <AutoComplete
                    onSearch={onSearch}
                    placeholder="Nama Pasien"
                    width="100%"
                    patterns
                    key="autocomplete_search"
                />
            ]}
        />
        <Main>
            <Row gutter={[25, 25]}>
                { loading ? (<>
                    <Col lg={12} xs={24}>
                        <Card>
                            <Skeleton avatar paragraph={{ rows: 4 }} />
                        </Card>
                    </Col>
                </>):
                data?.length === 0 ? (
                    <Col span={24}>
                        {alert}
                        <Card>
                            <Empty description="Tidak ada perjanjian hari ini"/>
                        </Card>
                    </Col>
                ) :
                data.map(row => {
                    return (
                        <Col lg={12} xs={24} key={row.appointment_id}>
                            <PatientCard patient={row} from="worklist" />
                        </Col>
                    );
                })
                }
                <Col span={24}>
                    <div className="text-right">
                        <Pagination
                            current={raw?.current_page}
                            defaultPageSize={25}
                            total={raw?.total}
                            onChange={changePagination}
                            onShowSizeChange={changePagination}
                            showTotal={total => `Total ${total} Perjanjian`}
                            showQuickJumper={true}
                            showSizeChanger={true}
                        />
                    </div>
                </Col>
            </Row>
        </Main>
    </>);
}

export default Worklist;