import React from 'react';
import { Switch, Route, useRouteMatch } from 'react-router-dom';
import withAdminLayout from '../../layout/withAdminLayout';

import Worklist from '../../container/appointment/worklist';
import AppointmentDetail from '../../container/appointment/detail';
import AppointmentStart from '../../container/appointment/start';
import AppointmentHistory from '../../container/appointment/history';
import AppointmentIncoming from '../../container/appointment/appointment';
import Setting from '../../container/profile/setting';
import Dashboard from '../../container/dashboard';
import SoapUpdate from '../../container/appointment/soap';

const Admin = () => {
  const { path } = useRouteMatch();

  return (
    <Switch>
      <Route exact path={path} component={Dashboard} />
      <Route exact path={`${path}/worklist`} component={Worklist} />
      <Route exact path={`${path}/worklist/:id`} component={AppointmentDetail} />
      <Route exact path={`${path}/worklist/:id/start`} component={AppointmentStart} />
      <Route exact path={`${path}/history`} component={AppointmentHistory} />
      <Route exact path={`${path}/history/:id`} component={AppointmentDetail} />
      <Route exact path={`${path}/history/soap/:id`} component={SoapUpdate} />
      <Route exact path={`${path}/appointment`} component={AppointmentIncoming} />
      <Route exact path={`${path}/appointment/:id`} component={AppointmentDetail} />
      <Route exact path={`${path}/setting`} component={Setting} />
    </Switch>
  );
};

export default withAdminLayout(Admin);
